.container {
    max-width: 100%;
   
    padding-top: 50px;
    background-image: url('https://i.imgur.com/6n8ZkKR.png');
    background-repeat: no-repeat; /* Không lặp */
  background-size: cover;     /* Co giãn để phủ kín phần tử */
}

.card {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

h2 {
    color: #007bff;
}
@keyframes colorChange {
    0% { 
        transform: translate3d(100%, 100%, 0) scale(0.5); /* Xuất phát từ góc dưới bên phải */
        opacity: 0; /* Ẩn chữ */
    }

    60% {
        transform: translate3d(-10%, -10%, 0) scale(1.05); /* Lắc nhẹ */
        opacity: 0.8;
    }

    100% { 
        transform: translate3d(0, 0, 0) scale(1); /* Về vị trí ban đầu */
        opacity: 1; /* Hiển thị hoàn toàn */
    }
}


.fromdk {
    background-color:#1E293B ;
    animation: colorChange 3s ease-in-out; 
}

.form-control { 
    background-color : #0F172A;
}

.form-control:hover { 
 border-style:dashed;
 box-shadow: 1px 1px 3px 5px #5B21B6;
}


