
.parentla {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    gap: 8px;
    background-color: #0F172A;
    height: 700px;
}
    
   
@media screen and (max-width: 768px) {
    
.div1la { 
    background-color: #1E293B;
    width: 80%;
    height: 40%;
    border-radius: 30px;
    text-align: center;
    position: relative;
    left: 50px;
    top: 100px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    align-content: center;
}
     
}

@media screen and (min-width: 768px) {
    
    .div1la { 
        background-color: #1E293B;
        width: 80%;
        height: 40%;
        border-radius: 30px;
        text-align: center;
        position: relative;
        left: 100px;
        top: 100px;
        
        color: white;
        font-weight: bold;
        font-size: 20px;
        align-content: center;
    }
         
    }
