
.parentf {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 8px;
   
}
    
.contai-ruttien {
    background-color:#0F172A;
}


        
@media screen and (max-width: 768px) {
        

.soducuaban {
font-family: "Kanit", sans-serif;
box-shadow: 3px 3px 7px #5B21B6;
font-size : 25px;
background: linear-gradient(45deg, #5B21B6,  #1F419B, #1E293B);
width: 40%;
align-content: center;
border: 0px solid black;
border-radius: 30px;
height: 130px;
position:  relative;
left: 25%;
animation: colorChange 2s ease-in-out; 
text-shadow: 2px 2px 10px #0e0811;
}

@keyframes colorChange { 
    0% {
    
        transform: translate3d(-100%, -100%, 0);
        
    }  
    100% {
       
    }
}
.soducuaban:hover {
    background: linear-gradient(45deg, #1E293B,  #1F419B, #5B21B6);
    box-shadow: 3px 3px 7px #5B21B6;
    animation: colorChange 2s ease-in-out;
    text-shadow: 2px 2px 10px #0e0811;
    transform: scale(1.1);

}

.withdrawal-form-containerr { 
    background-color: #1E293B;
    position: relative;
    bottom: 75%;
    width: 470px;
    height: 600px;
    left: 10px;
   
    border: 0px solid black;
    border-radius: 30px;
}
.ruttienvenganhang {
    font-size: 25px;
    font-weight: bold;
    position: relative;
    top: 10px;
    color: #F0F4F9;
}

.withdrawal-formm { 
    position: relative;
    top: 50px;
    left: 50px;
    width: 300px;
    height: 500px;
    border: 0px solid black;
    border-radius: 30px;
    background-color: #1E293B;
}


.bangruttien { 
    margin: 10px;
    position: relative;
    display: inline-block;
}
.nhapgiatriruttien{
    width: 400px;
    height: 40px;
    padding: 10px 40px; /* Chừa khoảng trống bên trái cho icon */
    border: 1px solid #ccc;
    border-radius: 20px;
    background-color: #1E293B;
    font-weight: bold;
    

}
.iconruttien { 
    font-size: 20px;
}

.nutruttien{
    position: relative;
    top: 50px;
    border: 0px solid black;
    width: 400px;
    height : 70px;  
  
    border-radius: 20px;
    color: white;
    font-weight: bold;
}


.hienthinganhang { 
    display: flex;
     flex: left;
     gap: 10px;
}

.hienthinganhang img {
    width: 90px;
    height: 50px;
   border: 0px solid black;
   border-radius: 10px;
    margin-right: 10px;
}

.banghuongdan { 
    width: 450px;
    height: 700px;
    background-color: #F0F4F9;
    position: relative;
    bottom: 600px;
    left: 25px;
    border: 0px solid black;
    border-radius: 20px;
    background-color: #1E293B;
}
}



