
.parentc {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 8px;
}
    
.div1c {
    grid-row: span 5 / span 5;
}

.div2c {
    grid-row: span 5 / span 5;
}

.div3c {
    grid-row: span 5 / span 5;
}

.div4c {
    grid-row: span 5 / span 5;
}


 