@keyframes colorChange {
    0% { 
        /* Tại thời điểm bắt đầu */
      
        transform: translateY(-100%);  /* Di chuyển chữ ra ngoài phía trên */
        opacity: 0; /* Chữ không hiển thị */
    }

    30% {
     
        transform: translateY(30%); /* Chữ di chuyển xuống vị trí trung gian */
        opacity: 1; /* Chữ hiển thị hoàn toàn */
    }
    
    40% {
       
        transform: translateY(0); /* Chữ về vị trí ban đầu */
        opacity: 1; /* Chữ hiển thị hoàn toàn */
    }

    50% {
      
        transform: translateY(30%); /* Chữ di chuyển xuống vị trí trung gian */
        opacity: 1; /* Chữ hiển thị hoàn toàn */
    }

    60% {
      
        transform: translateY(0); /* Chữ về vị trí ban đầu */
        opacity: 1; /* Chữ hiển thị hoàn toàn */
    }

    80% { 
        /* Khi chuyển động đến cuối chu kỳ */
      
        opacity: 0; /* Chữ dần dần ẩn đi */
        transform: translateY(100%);  /* Chữ di chuyển ra ngoài phía dưới */
    }

    100% { 
        /* Khi chuyển động đến cuối chu kỳ */
      
        opacity: 1; /* Chữ dần dần ẩn đi */
        transform: translateY(0);  /* Chữ di chuyển ra ngoài phía dưới */
    }
}



.parent {
    animation: colorChange 5s ease-in-out; 
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 20px;
}
    
.div1 {
    transition: transform 0.3s ease;
    grid-column: span 2 / span 2;
    grid-row: span 3 / span 3;
    background-color:#1E293B ;
    border: 0px solid black;
    border-radius: 10px;
    box-shadow: 3px 1px 5px 1px rgb(212, 210, 210);
}
.div1:hover { 
    display:inline-block;
    transform: scale(1.1); 
}

.div4 {
    transition: transform 0.3s ease;
    grid-column: span 2 / span 2;
    grid-row: span 3 / span 3;
    grid-column-start: 3;
    background-color:#1E293B ;
    border: 0px solid black;
