
.parentd {

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(8, 1fr);
   gap: 10px;
    width:100%;
   height: 1000px;
    background-color: #0F172A;

}
    
.div1d {
  
    grid-column: span 5 / span 5;
    background-color: #1E293B;
}

.div2d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 3 / span 3;
    grid-row: span 2 / span 2;
    grid-row-start: 2;
    background-color: #1E293B;
}

.div3d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 2 / span 2;
    grid-row: span 2 / span 2;
    grid-column-start: 4;
    grid-row-start: 2;
    background-color: #1E293B;
}

.div4d {
    grid-column: span 5 / span 5;
    grid-row-start: 4;
    background-color: #1E293B;
}

.div5d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 3 / span 3;
    grid-row-start: 5;
    background-color: #1E293B;
}

.div6d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 2 / span 2;
    grid-column-start: 4;
    grid-row-start: 5;
    background-color: #1E293B;
}

.div10d {
    
    grid-column: span 5 / span 5;
    grid-row-start: 6;
    background-color: #1E293B;
}

.div11d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 3 / span 3;
    grid-row-start: 7;
    background-color: #1E293B;
}

.div12d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 2 / span 2;
    grid-column-start: 4;
    grid-row-start: 7;
    background-color: #1E293B;
}

.div13d {
    border: 0px  solid  black;
    border-radius: 50px;
    grid-column: span 5 / span 5;
    grid-row-start: 8;
    background-color: #1E293B;
}
      
