/* Modal Styles */
.modal {
    display: none; /* Ẩn modal theo mặc định */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  
  .modal-dialog {
    max-width: 500px;
    width: 100%;
  }
  
  .modal-content {
    background-color: #fff;
    border-radius: 5px;
    padding: 20px;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .modal-body {
    padding: 10px 0;
  }
  
  .modal-footer {
    display: flex;
    justify-content: flex-end;
  }
  
