body {
   
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 100%;
   

       
    
}



/* Header */
#header-mobile { 
    display: none;
   
}

@media screen and (max-width: 999px) {
    #header-mobile {
    display: flex;
    float:left;
    }
    #header-pc  { 
        display: none;
    }
}
@media screen and (min-width : 1000px) {
    #header-mobile {
        display: none;
    } 
    #header-pc {
        display: flex;
        float:left;
        gap: 50px;
        font-size: 20px;

    }

    
}


/* Category */
#Category-container {
    background-color: #0F172A;
    border: 1px solid black;
    height: 400px;
}




/* section */


#Section-container {
    background-color: #0F172A;
    border: 1px solid black;
    height: 1600px;
    display: grid;
    grid-template-columns: 2fr 4fr;
    grid-template-rows : 2fr 6fr 3fr;
    grid-template-areas:
    "Soduuser Soduuser"
    "Tintuc Tintuc"
    "Khaosat Moibanbe";
}
#Soduuser {
    border: 1px solid black;
    background-color: #0F172A;
    grid-area: Soduuser;
}
 #Tintuc { 
   grid-area: Tintuc;
    border: 1px solid black;
    background-color: #0F172A;
}

#Moibanbe { 
    grid-area: Moibanbe;
    border: 1px solid black;
    background-color: #0F172A;
}
#Khaosat {
    grid-area: Khaosat;
    border: 1px solid black;
    background-color: #0F172A;
}







#ListProduct-container {
    background-color: #0F172A;
    border: 1px solid black;
    height: auto;
}

#Footer-container {
  
    background-color: #0F172A;
    height: 300px;
}


.button-btn { 
    border: 0px soild black;
    border-radius: 20px;
    background-color: #0F172A;

}
.button-btn:hover { 
background-color: #6D28D9;
transform: scale(1.1); 
transition: transform 0.3s ease;
box-shadow: 3px 1px 5px 1px rgb(161, 12, 190);
}

.phongto {
   border: 0px soild black;
   border-radius: 50px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.phongto:hover { 
    transform: scale(1.1); 
    box-shadow: 0 5px 15px rgba(34, 4, 28, 0.3);
}

.input-dep { 
    background-color: #6D28D9;
    box-shadow: 0 5px 15px rgba(34, 4, 28, 0.3);
    border-radius: 10px;
    border: 0px solid black;

}
.input-dep:hover {
    background-color: #0F172A;
    color: white;
    box-shadow: 3px 1px 5px 1px rgb(161, 12, 190);
    transform: scale(1.1); 
    border: 1px solid rgb(30, 25, 25);


}


@media screen and (max-width: 640px) { 
    #Footer-container { 
        display: none;
    }

    #ListProduct-container { 
        display: none;
    }

}